<template>
  <md-layout>
    <h1 class="md-headline">
      {{sections.about.heading}}
      <br/>
      <span class="md-caption" v-html="sections.about.subheading"></span>
    </h1>
    <md-whiteframe md-elevation="1" class="full-width">
      <div class="padded-container">
        <!--<h2 class="md-title">Summary</h2>-->
        <p v-html="sections.about.summary"></p>
        <h2 class="md-title">Objective</h2>
        <p v-html="sections.about.objective"></p>
        <br/>
        <br/>
        <!--<h2 class="md-title">Social Links</h2>-->
        <div class="social-icons">
          <div>
            <a v-for="profile in myInfo.profiles" :href="profile.url">
              <md-icon>{{profile.icon}}</md-icon>&nbsp; {{profile.title}}
            </a>
          </div>
        </div>
      </div>
      <br/>
    </md-whiteframe>
    <!--<md-avatar class="md-large">
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  <img src="~assets/img/myphoto.jpg" alt="My Picture">
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                </md-avatar>-->
  </md-layout>
</template>
<script>
  import { mapGetters } from 'vuex'

  export default {
    computed: {
      ...mapGetters(['myInfo', 'sections'])
    }
  }
</script>
<style scoped>
  .social-icons {
    width: 100%;
    display: table;
    text-align: center;
  }
  
  .social-icons>div {
    display: table-row;
  }
  
  .social-icons a {
    display: table-cell;
    padding: 10px;
  }
  
  .social-icons a:hover {
    text-decoration: none;
  }
</style>



