import Vue from 'vue'
import VueMaterial from 'vue-material'

Vue.use(VueMaterial)

Vue.material.registerTheme({
  default: {
    primary: 'indigo',
    accent: 'pink',
    warn: 'red',
    background: 'white'
  },
  invert: {
    primary: 'pink',
    accent: 'indigo',
    warn: 'red',
    background: 'white'
  },
  'theme-danger': {
    primary: 'red',
    accent: 'indigo',
    warn: 'red',
    background: 'white'
  },
  'theme-warn': {
    primary: 'deep-orange',
    accent: 'indigo',
    warn: 'red',
    background: 'white'
  },
  'theme-normal': {
    primary: 'orange',
    accent: 'indigo',
    warn: 'red',
    background: 'white'
  },
  'theme-primary': {
    primary: 'lime',
    accent: 'indigo',
    warn: 'red',
    background: 'white'
  },
  'theme-success': {
    primary: 'green',
    accent: 'indigo',
    warn: 'red',
    background: 'white'
  }
})



